

cc.Class({
    extends: cc.Component,

    //声明公共变量方法
    properties:
    {
        pickRadius: 0,
    },

    // LIFE-CYCLE CALLBACKS:

    //#region  获取玩家与本物体的距离方法
    getPlayerDistance()
    {
        //获取玩家位置
        var playerPos = this.game.player.getPosition();
        //获取玩家与本物体的距离
        var dist = this.node.position.sub(playerPos).mag();

        //返回本物体与玩家的距离数值
        return dist;
    },
//#endregion  获取玩家与本物体的距离方法end

//#region  生成新星并销毁本物体方法
    onPicked()
    {
        //调用方法生成新星
        this.game.spawnNewStar();
        //调用 Game_script 脚本的更新得分显示方法方法
        this.game.gainScore();
        //销毁本物体
        this.node.destroy();
    },
//#endregion  生成新星并销毁本物体方法end

    //每帧调用
     update (dt) 
     {
         //玩家的位置与本物体 < 60
         if(this.getPlayerDistance() < this.pickRadius)
         {
             //调用
             this.onPicked();
             return;
         }

         /*
        var opacityRatio = 1 - this.game.timer/this.game.starDuration;
        var minOpacity = 50;
        //本物体.不透明度 = 
        this.node.opacity = minOpacity + Math.floor(opacityRatio * (255 - minOpacity));
        */

        // 根据 Game 脚本中的计时器更新星星的透明度
        var opacityRatio = 1 - this.game.timer/this.game.starDuration;
        var minOpacity = 50;
        this.node.opacity = minOpacity + Math.floor(opacityRatio * (255 - minOpacity));
        //cc.log("1");
        
     },

});
